.custom_cursor{
  width: 50px;
  height: 50px;
  position: fixed;
  background-color: var(--text-color-primary);
  border-radius: 50%;
  -webkit-filter: invert(100%);
          filter: invert(100%);
  z-index: 1000;
}


.custom_cursor_chase {
  width: 74px;
  height: 74px;
  position: fixed;
  z-index: 9999;
  transition: all 1s ease-out;
  border-radius: 50%;
}

