.contact_me_button {
    margin-top: 40px;
    border: none;
    color: var(--text-color-secondary);
    padding: 10px 30px;
    border-radius: 50px;
    text-align: center;
    font-family: 'Raleway-Bold', sans-serif;
    position: relative;
    overflow: hidden;
    background: linear-gradient(to right, var(--secondary-color), var(--secondary-color-secondary-double-extra-dark));
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.2);
    transition: box-shadow 0.3s ease, -webkit-transform 0.3s ease;
    transition: transform 0.3s ease, box-shadow 0.3s ease;
    transition: transform 0.3s ease, box-shadow 0.3s ease, -webkit-transform 0.3s ease;
}

.contact_me_button::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: var(--secondary-color);
    z-index: -1;
    -webkit-transform: scaleX(0);
            transform: scaleX(0);
    -webkit-transform-origin: bottom left;
            transform-origin: bottom left;
    transition: -webkit-transform 0.3s ease;
    transition: transform 0.3s ease;
    transition: transform 0.3s ease, -webkit-transform 0.3s ease;
    border-color: var(--text-color-primary);
}

.contact_me_button:hover {
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
    box-shadow: 0 0 20px rgba(0, 0, 0, 0.4);
    transition: -webkit-transform 0.3s ease;
    transition: transform 0.3s ease;
    transition: transform 0.3s ease, -webkit-transform 0.3s ease;
}

.contact_me_button:hover::before {
    -webkit-transform: scaleX(1);
            transform: scaleX(1);
    -webkit-transform-origin: bottom right;
            transform-origin: bottom right;
    border-color: var(--text-color-primary);
    transition: -webkit-transform 0.3s ease;
    transition: transform 0.3s ease;
    transition: transform 0.3s ease, -webkit-transform 0.3s ease;
}

@-webkit-keyframes pulse {

    0%,
    100% {
        -webkit-transform: scale(1);
                transform: scale(1);
    }

    50% {
        -webkit-transform: scale(1.1);
                transform: scale(1.1);
    }
}

@keyframes pulse {

    0%,
    100% {
        -webkit-transform: scale(1);
                transform: scale(1);
    }

    50% {
        -webkit-transform: scale(1.1);
                transform: scale(1.1);
    }
}

.contact_me_button:hover {
    -webkit-animation: pulse 3s infinite alternate;
            animation: pulse 3s infinite alternate;
}


/* Layer  */

